import { Edit, RefreshCw, Trash2, Power, Copy, ExternalLink } from "lucide-react"
import type { AdminVPNClient } from "@/lib/api"
import { useState } from "react"

interface KeyTableProps {
    keys: AdminVPNClient[]
    onEdit: (key: AdminVPNClient) => void
    onExtend: (id: number) => void
    onDelete: (id: number) => void
    onToggle: (id: number) => void
}

const formatDate = (value?: string | null) => {
    if (!value) return "‚Äî"
    return new Date(value).toLocaleDateString("ru-RU", {
        day: "2-digit",
        month: "2-digit",
        year: "numeric",
        hour: "2-digit",
        minute: "2-digit",
    })
}

const formatTraffic = (usedGb: number, limitGb: number) => {
    if (limitGb === 0) {
        return `${usedGb.toFixed(1)} –ì–ë / ‚àû`
    }
    return `${usedGb.toFixed(1)} / ${limitGb.toFixed(0)} –ì–ë`
}

const getStatusBadge = (status: string, isExpired: boolean) => {
    if (isExpired) {
        return <span className="text-[8px] px-2 py-1 bg-red-500/20 text-red-400">–ò—Å—Ç—ë–∫</span>
    }
    
    switch (status.toLowerCase()) {
        case "active":
            return <span className="text-[8px] px-2 py-1 bg-primary/20 text-primary">–ê–∫—Ç–∏–≤–µ–Ω</span>
        case "disabled":
            return <span className="text-[8px] px-2 py-1 bg-orange-500/20 text-orange-400">–û—Ç–∫–ª—é—á—ë–Ω</span>
        case "expired":
            return <span className="text-[8px] px-2 py-1 bg-red-500/20 text-red-400">–ò—Å—Ç—ë–∫</span>
        default:
            return <span className="text-[8px] px-2 py-1 bg-muted text-muted-foreground">{status}</span>
    }
}

export function KeyTable({ keys, onEdit, onExtend, onDelete, onToggle }: KeyTableProps) {
    const [copiedId, setCopiedId] = useState<number | null>(null)
    
    const handleCopy = async (text: string, id: number) => {
        await navigator.clipboard.writeText(text)
        setCopiedId(id)
        setTimeout(() => setCopiedId(null), 2000)
    }
    
    if (keys.length === 0) {
        return (
            <div className="bg-card border border-border p-8 text-center">
                <p className="text-muted-foreground text-sm">–ù–µ—Ç VPN –∫–ª—é—á–µ–π</p>
            </div>
        )
    }
    
    return (
        <div className="bg-card border border-border overflow-x-auto">
            <table className="w-full min-w-[900px]">
                <thead className="bg-secondary">
                    <tr>
                        <th className="px-3 py-3 text-left text-[8px] text-muted-foreground whitespace-nowrap">ID</th>
                        <th className="px-3 py-3 text-left text-[8px] text-muted-foreground whitespace-nowrap">–ü–û–õ–¨–ó–û–í–ê–¢–ï–õ–¨</th>
                        <th className="px-3 py-3 text-left text-[8px] text-muted-foreground whitespace-nowrap">MARZBAN ID</th>
                        <th className="px-3 py-3 text-left text-[8px] text-muted-foreground whitespace-nowrap">–£–°–¢–†–û–ô–°–¢–í–û</th>
                        <th className="px-3 py-3 text-left text-[8px] text-muted-foreground whitespace-nowrap">–ò–°–¢–ï–ö–ê–ï–¢</th>
                        <th className="px-3 py-3 text-left text-[8px] text-muted-foreground whitespace-nowrap">–¢–†–ê–§–ò–ö</th>
                        <th className="px-3 py-3 text-left text-[8px] text-muted-foreground whitespace-nowrap">–°–¢–ê–¢–£–°</th>
                        <th className="px-3 py-3 text-left text-[8px] text-muted-foreground whitespace-nowrap">–î–ï–ô–°–¢–í–ò–Ø</th>
                    </tr>
                </thead>
                <tbody>
                    {keys.map((key) => (
                        <tr key={key.id} className="border-t border-border hover:bg-secondary/50">
                            <td className="px-3 py-3 text-[9px] text-muted-foreground">#{key.id}</td>
                            <td className="px-3 py-3">
                                {key.user ? (
                                    <div>
                                        <div className="text-[9px] text-foreground">{key.user.username}</div>
                                        <div className="text-[8px] text-muted-foreground">{key.user.email || "‚Äî"}</div>
                                    </div>
                                ) : (
                                    <span className="text-[9px] text-muted-foreground italic">–ë–µ–∑ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏</span>
                                )}
                            </td>
                            <td className="px-3 py-3">
                                <div className="flex items-center gap-1">
                                    <code className="text-[8px] text-primary font-mono truncate max-w-[100px]" title={key.marzban_username}>
                                        {key.marzban_username || key.client_uuid.slice(0, 8)}
                                    </code>
                                    {key.subscription_url && (
                                        <button
                                            onClick={() => handleCopy(key.subscription_url!, key.id)}
                                            className="text-muted-foreground hover:text-primary"
                                            title="–ö–æ–ø–∏—Ä–æ–≤–∞—Ç—å VLESS"
                                        >
                                            {copiedId === key.id ? (
                                                <span className="text-[8px] text-green-500">‚úì</span>
                                            ) : (
                                                <Copy size={12} />
                                            )}
                                        </button>
                                    )}
                                </div>
                            </td>
                            <td className="px-3 py-3 text-[9px] text-foreground">{key.name || key.device_info || "‚Äî"}</td>
                            <td className="px-3 py-3">
                                <div>
                                    <div className={`text-[9px] ${key.is_expired ? 'text-red-400' : 'text-foreground'}`}>
                                        {formatDate(key.expire_date)}
                                    </div>
                                    {key.time_remaining && (
                                        <div className={`text-[8px] ${key.is_expired ? 'text-red-400' : 'text-muted-foreground'}`}>
                                            {key.time_remaining}
                                        </div>
                                    )}
                                </div>
                            </td>
                            <td className="px-3 py-3 text-[9px] text-foreground">
                                {formatTraffic(key.traffic_used_gb, key.traffic_limit_gb)}
                            </td>
                            <td className="px-3 py-3">
                                {getStatusBadge(key.status, key.is_expired)}
                            </td>
                            <td className="px-3 py-3">
                                <div className="flex items-center gap-1">
                                    <button 
                                        onClick={() => onToggle(key.id)} 
                                        className={`p-1 rounded ${key.status === 'active' ? 'text-green-500 hover:bg-green-500/10' : 'text-muted-foreground hover:bg-muted'}`}
                                        title={key.status === 'active' ? '–û—Ç–∫–ª—é—á–∏—Ç—å' : '–í–∫–ª—é—á–∏—Ç—å'}
                                    >
                                        <Power size={14} />
                                    </button>
                                    <button
                                        onClick={() => onExtend(key.id)}
                                        className="p-1 rounded text-muted-foreground hover:text-accent hover:bg-accent/10"
                                        title="–ü—Ä–æ–¥–ª–∏—Ç—å –Ω–∞ 30 –¥–Ω–µ–π"
                                    >
                                        <RefreshCw size={14} />
                                    </button>
                                    <button 
                                        onClick={() => onEdit(key)} 
                                        className="p-1 rounded text-muted-foreground hover:text-primary hover:bg-primary/10" 
                                        title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å"
                                    >
                                        <Edit size={14} />
                                    </button>
                                    <button
                                        onClick={() => onDelete(key.id)}
                                        className="p-1 rounded text-muted-foreground hover:text-red-400 hover:bg-red-400/10"
                                        title="–£–¥–∞–ª–∏—Ç—å"
                                    >
                                        <Trash2 size={14} />
                                    </button>
                                </div>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
            
            {/* Summary */}
            <div className="border-t border-border px-4 py-2 bg-secondary/50 flex items-center justify-between">
                <span className="text-[9px] text-muted-foreground">
                    –í—Å–µ–≥–æ –∫–ª—é—á–µ–π: {keys.length}
                </span>
                <span className="text-[9px] text-muted-foreground">
                    –ê–∫—Ç–∏–≤–Ω—ã—Ö: {keys.filter(k => k.status === 'active' && !k.is_expired).length} | 
                    –ò—Å—Ç–µ–∫—à–∏—Ö: {keys.filter(k => k.is_expired).length}
                </span>
            </div>
        </div>
    )
}
